<template>
 <div>
  <div v-if="!markbg" class="markbg"><img src="../assets/play.gif" alt=""></div>
  <div v-if="markbg" class="detail">
    <div class="bg" ref="bg" :style="playlist && `background-image: url(${playlist.coverImgUrl})`">
      <span class="back" @click="back"><i class="fa fa-angle-left"></i></span>
      <h1 class="sgb">共{{playlist.tracks.length}}首歌曲</h1>
      <div class="bg-left">
        <img :src="playlist.coverImgUrl" alt="" />
        <span v-if="isEdit"><div class="boxcc"><img src="../assets/play-kw.png" alt="" /></div><p>{{playVolume}}</p></span>
        <nav v-if="isEdit">歌单</nav>
      </div>
      <div class="bg-right">
        <p class="name">{{ playlist.name }}</p>
        <p class="avatars">
          <img :src="creator.avatarUrl" alt="" /><img :src="avatarDetail.identityIconUrl" alt=""><span>{{ creator.nickname }}</span>
        </p>
        <!-- <div><img :src="playlist.creator.avatarUrl" alt=""></div>
                <span>{{creator.nickname}}</span> -->
      </div>
    </div>
    <ul>
      <DetailList v-for="(track,index) in playlist.tracks" :key="track.id" :song="song" :track="track" :index="index+1" @updata-song="$emit('updata-song',$event)"></DetailList>
    </ul>
    <h1 v-if="isEdit">精彩评论</h1>
    <ul>
      <HotcommentList v-for="hotComment in comment.hotComments" :key="hotComment.commentId" :hotComment="hotComment"></HotcommentList>
    </ul>
  </div>
 </div>
</template>

<script>
import DetailList from "@/components/detail/DetailList.vue";
import HotcommentList from "@/components/detail/HotcommentList.vue"
export default {
  components: {
    DetailList,
    HotcommentList
  },
  props:["song"],
  data() {
    return {
      creator: [],
      playlist: [],
      comment: [],
      avatarDetail:[],
      isEdit:false,
      markbg:false
    };
  },
  computed: {
    playVolume() {
      return this.playlist.playCount > 100000000
        ? `${(this.playlist.playCount / 100000000).toFixed(1)}亿`
        : this.playlist.playCount > 10000
        ? `${(this.playlist.playCount / 10000).toFixed(1)}万`
        : this.playlist.playCount;
    },
  },
  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
        params: {
          id: this.$router.history.current.query.id,
        },
      }),
      this.axios.get("https://apis.netstart.cn/music/comment/playlist/", {
        params: {
          id: this.$router.history.current.query.id,
        },
      }),
    ]).then(([d, c]) => {
      this.playlist = d?.data?.playlist 
      this.comment = c?.data;
      this.creator = d?.data?.playlist?.creator;
      this.markbg=true
      this.isEdit=true
      this.avatarDetail=d?.data?.playlist?.creator?.avatarDetail
    });
  },
  methods:{
    back(){
      this.$router.back()
    }
  }
  // watch: {
  //   playlist(news) {
  //     if (news) {
  //       this.$refs.bg.style.backgroundImage = `url(${this.playlist.coverImgUrl})`;
  //     }
  //   },
  // },
};
</script>

<style lang="less" scoped>
.markbg{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.detail{
  h1{
    padding: 5rem 20rem;
    text-align: left;
    font-size: 24rem;
    font-weight: bold;
    background-color: #fff;
    display: flex;
    align-items: center;
    height: 60rem;
    border-top-left-radius: 15rem;
    border-top-right-radius: 15rem;
    margin-top: 20rem;
  }
  .bg {
  height: 240rem;
  background-size: cover;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  &::before {
    content: "";
    width: 120%;
    height: 120%;
    filter: blur(15rem) brightness(70%);
    background-image: inherit;
    background-size: cover;
    position: absolute;
    top: -15px;
    left: -15px;
    z-index: 1;
  }
  .back{
    width: 40rem;
    height: 40rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    i{
      font-size: 30rem;
      color: #fff;
    }
  }
  .sgb{
    display: block;
    width: 100%;
    position: absolute;
    height: 40rem;
    padding: 5rem 20rem;
    left: 0;
    bottom: 0;
    font-size: 24rem;
    font-weight: bold;
    text-align: left;
    background-color: #fff;
    z-index: 300;
    border-top-left-radius: 15rem;
    border-top-right-radius: 15rem;
  }
  .bg-left {
    height: 126rem;
    width: 126rem;
    position: relative;
    margin-left: 10rem;
    border-radius: 20rem;
    img {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 999;
      border-radius: 20rem;
    }
    span {
      display: block;
      width: 70%;
      height: 30rem;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1000;
      color: #fff;
      background-color: rgba(0,0,0,0.7);
      display: flex;
      justify-content: start;
      align-items: center;
      border-top-right-radius: 20rem;
      border-bottom-left-radius: 20rem;
      padding: 0 10rem;
      .boxcc{
        width: 15rem;
        height: 15rem;
        position: relative;
        img {
          width: 12rem;
        }
      }
    }
    nav{
        position: absolute;
        top: 15rem;
        left: 0;
        width: 40rem;
        height: 17rem;
        background-color: rgba(255, 0, 0, .5);
        z-index: 1000;
        color: #fff;
        border-top-right-radius: 8rem;
        border-bottom-right-radius: 8rem;
    }
  }
  .bg-right {
    width: 208rem;
    height: 126rem;
    position: relative;
    color: #fff;
    margin-left: 10rem;
    .name {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 999;
      font-size: 16rem;
      text-align: left;
    }
    .avatars {
      position: absolute;
      top: 60rem;
      left: 0;
      z-index: 999;
      display: flex;
      justify-content: left;
      align-items: center;
      img {
        width: 30rem;
        border-radius: 50%;
      }
      img:nth-of-type(2){
        position: absolute;
        bottom: -2rem;
        left: 20rem;
        width: 14rem;
      }
      span {
        font-size: 15rem;
        color: #d3d3d3;
        margin-left: 10rem;
      }
    }
  }
}
}
</style>