<template>
  <div>
    <div v-if="!markbg" class="markbg">
      <img src="../assets/play.gif" alt="" />
    </div>
    <div v-if="markbg" class="about">
      <div class="bg">
        <img src="../assets/newbg1.png" alt="">
        <p>更新时间:{{ timer }}</p>
      </div>
    </div>
    <ul class="PlayList">
       <div v-if="playlists">共{{playlists?.length}}首歌曲</div>
        <PlayList
          v-for="(playlist, index) in playlists"
          :key="playlist.id"
          :playlist="playlist"
          :indexs="index + 1"
          @updata-song="$emit('updata-song', $event)"
          :song="song"
        />
      </ul>
  </div>
</template>

<script>
import PlayList from "@/components/PlayList.vue"
export default {
    data(){
        return{
            playlists:null,
            markbg:false
        }
    },
    components: {
      PlayList
    },
    props:['song'],
    computed: {
    timer() {
      return new Date().getMonth() + 1 + "月" + new Date().getDate() + "日";
      },
    },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail", {
        params: { id: 3779629 },
      })
      .then((res) => {
        this.playlists=res.data.playlist.tracks
        this.markbg=true
      });
  },
};
</script>

<style lang="less" scoped>
.markbg{
  width: 100vw;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about {
  padding: 25rem 10rem;
  height: 230rem;
  background-color: #45b3ee;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .bg {
    width: 200rem;
    position: relative;
    p {
      width: 100rem;
      position: absolute;
      bottom: 30rem;
      left: 50%;
      transform: translateX(-60%);
      margin-left: 10rem;
      text-align: left;
      color: #fff;
    }
  }
}
.PlayList {
    width: 100%;
    background-color: #fff;
    position: relative;
    top: -20rem;
    border-top-left-radius: 15rem;
    border-top-right-radius: 15rem;
    li:nth-child(2),
    li:nth-child(3),
    li:nth-child(4) {
      color: #e13436;
    }
    div{
      width: 100%;
      height: 50rem;
      display: flex;
      justify-content: left;
      align-items: center;
      padding-left: 20rem;
      font-size: 24rem;
      font-weight: bold;
    }
  }
</style>