<template>
  <div class="SearchView">
    <div class="search">
      <div class="search-top">
        <label for="search"><img src="../assets/search.svg" alt="" /></label>
      <input
        type="text"
        name="search"
        id="search"
        placeholder="搜索你想听的音乐"
        ref="input"
        v-model="value"
        autocomplete="off"
        @keydown.enter="addtodo(value)"
      />
      <span v-if="value" @click="clearValue" class="close"><img src="../assets/close.svg" alt="" /></span>
      </div>
      <div class="search-footer"><span @click="goHome" v-if="value==''">取消</span><span v-if="value" @click="addtodo(value)">搜索</span></div>
    </div>
    <ul v-if="value == ''" class="Details">
      <SearchDetails v-for="todo in todos" :key="todo.id" :todo="todo" @cleartodo="cleartodo" @addtodo="addtodo"/>
    </ul>
    <h3 v-if="value == ''">热门搜索</h3>
    <ul v-if="value == ''" class="hots">
      <SearchList v-for="(hot,index) in hots" :key="hot.id" :hot="hot" @addtodo="addtodo" :index="index+1" />
    </ul>
    <!-- v-show="" -->
    <h1 v-if="value && !isEist" >搜索"{{ value }}"</h1>
    <div v-if="!markbgs && value && !isEist" class="markbg"><img src="../assets/play.gif" alt=""></div>
    <ul v-if="value && !isEist && markbgs" class="keywords">
      <SearchItem v-for="(keyword, index) in keywords" :key="index" :keyword="keyword" @addtodo="addtodo" />
    </ul>
    <div v-if="!markbg && isEist" class="markbg"><img src="../assets/play.gif" alt=""></div>
    <ul v-if="isEist && markbg" class="artist">
      <Artist :artist="artist" :song="song" :songs="songs" @updata-song="$emit('updata-song',$event)"/>
    </ul>
  </div>
</template>

<script>
import SearchList from "@/components/SearchList.vue";
import SearchItem from "@/components/SearchKey.vue";
import SearchDetails from "@/components/SearchDetails.vue";
import Artist from "@/components/artist/ArtIstion.vue";
import _ from "lodash";
// @ is an alias to /src

export default {
  name: "SearchView",
  components: {
    SearchList,
    SearchItem,
    SearchDetails,
    Artist,
  },
  props:["song"],
  data() {
    return {
      hots: [],
      value: "",
      keywords: [],
      todos: JSON.parse(localStorage.getItem("todos")) || [],
      artist: {},
      songs: [],
      isEist: false,
      markbg:false,
      markbgs:false
    };
  },
  created() {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
    this.$nextTick(() => {
      this.$refs.input.focus();
    });
    this.debouncegetkeywords = _.debounce(this.getkeywords, 800);
  },
  computed: {
    option() {
      return {
        keywords: this.value,
        type: "mobile",
      };
    },
  },
  methods: {
    getkeywords(option) {
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: option,
        })
        .then((res) => {
          this.keywords = res?.data?.result?.allMatch;
          this.markbgs=true
        });
    },
    clearValue() {
      this.value = "";
    },
    // 添加
    addtodo(first) {
      this.value = first;
      if (!this.todos.some((o) => o.text == first)) {
        this.todos.unshift({
          id: this.todos.length ? Math.max(...this.todos.map((o) => o.id)) + 1 : 1,
          text: first,
        });
      } else {
        this.todos = this.todos.filter((o) => {
          return o.text != first;
        });
        this.todos.unshift({
          id: this.todos.length ? Math.max(...this.todos.map((o) => o.id)) + 1 : 1,
          text: first,
        });
      }
      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/search/multimatch", {
          params: {
            keywords: first,
          },
        }),
        this.axios.get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords: first,
          },
        }),
      ]).then(([a, s]) => {
        this.artist = a?.data?.result;
        this.songs = s?.data?.result?.songs;
        this.markbg=true
      });
      this.isEist = true;
    },
    // 删除
    cleartodo(id) {
      this.todos = this.todos.filter((o) => {
        return o.id != id;
      });
    },
    goHome(){
      this.$router.back();
    }
  },
  watch: {
    option(news) {
      if (news.keywords) {
        this.debouncegetkeywords(news);
      } else {
        this.value = "";
      }
    },
    todos(news) {
      // console.log(1);
      localStorage.setItem("todos", JSON.stringify(news));
    },
    value(news) {
      if (news == "") {
        this.isEist = false;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.SearchView {
  display: flex;
  align-items: center;
  flex-direction: column;
  .markbg{
  width: 100vw;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
  .search {
    width: 100%;
    margin-top: 5rem;
    position: relative;
    height: 50rem;
    display: flex;
    align-items: center;
    justify-content: center;
    .search-top{
      position: relative;
      width: 75%;
    input {
      width: 100%;
      height: 38rem;
      border-radius: 15rem;
      border: 0;
      outline: none;
      background-color: #f2f4f5;
      -ms-input-placeholder: #a1acca;
      -webkit-input-placeholder: #a1acca;
      padding-left: 30rem;
    }
    label {
      width: 30rem;
      height: 30rem;
      position: absolute;
      top: 50%;
      left: 5rem;
      transform: translateY(-25%);
      img {
        width: 15rem;
        height: 15rem;
      }
    }
    .close {
      position: absolute;
      display: block;
      top: 50%;
      right: 15rem;
      transform: translateY(-50%);
      width: 15rem;
      height: 15rem;
    }
    }
    .search-footer{
      width: 15%;
      height: 38rem;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 18rem;
      color: #92919b;
    }
  }
  h1 {
    width: 100vw;
    height: 40rem;
    border-bottom: 2rem solid #f3f4f5;
    display: flex;
    align-items: center;
    font-size: 16rem;
    text-align: left;
    padding-left: 10rem;
    color: #507daf;
  }
  h2{
    width: 100vw;
    height: 40rem;
    font-size: 24rem;
    font-weight: bold;
  }
  h3 {
    width: 100vw;
    font-size: 24rem;
    font-weight: bold;
    text-align: left;
    margin: 20rem 0 10rem 30rem;
  }
  .hots {
    width: 100vw;
    li{
      color: #a1a4b3;
    }
    li:nth-child(1){
        color: #f84824;
    }
    li:nth-child(2){
        color: #f47f1b;
    }
    li:nth-child(3){
        color: #ffb143;
    }
  }
  .Details{
    width: 90vw;
    display: flex;
    justify-self: start;
    flex-wrap: wrap;
  }
  .keywords {
    width: 100%;
    margin-top: 10rem;
    li{
      width: 100%;
    }
  }
  .artist{
    width: 100%;
  }
}
</style>